.wrapper {
    width: 400px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    border-radius: 5px;
    padding: 10px 15px;
    margin-bottom: 10px;
  }
  .input {
    margin-bottom: 10px;
    padding: 8px;
    width: 95%;
    outline-color: #4545fc;
    font-weight: bold;
  }
  